main {
	width: 100vw;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-top: 15vh;
	height: calc(100vh - 56px); /* router view - navbar height */
	overflow: scroll;
	color: white;
}

.card {
	border-radius: 0;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
	margin-bottom: 15vh;
}

.Error {
	color: black;
	font-size: 3rem;
}
